<template>
  <div class="status-page">
    <Header :text="title"></Header>
    <div class="status-top" v-if="status">
      <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/success_tips.png" alt="">
      <h6>{{fromFlag ? '添加成功' : '签约成功'}}</h6>
      <p v-if="!fromFlag">您已成功签约物联支付功能</p>
    </div>
    <div class="status-top warning-top" v-else>
      <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/fail_tips.png" alt="">
      <h6>签约失败</h6>
      <p>{{getFreeMessage}}</p>
    </div>
    <div class="state-btn">
      <a href="javascript:;" @click="goBack">返回</a>
    </div>
  </div>
</template>

<script>
import UplusApi from '@uplus/uplus-api';
import Header from '@/components/header'
import { mapGetters } from 'vuex'
const instance = new UplusApi();
instance.initDeviceReady()
export default {
  name: 'freestate',
  components: {
    Header,
  },
  data() {
    return {
      title: '',
      status: '',
      fromFlag: '', // 来源标识  收银台绑卡 or 免密签约
    }
  },
  mounted() {
    this.status = +this.$route.query.status;
    this.fromFlag = this.$route.query.addcard

    if (this.fromFlag) {
      this.title = '添加银行卡'
    } else {
      this.title = '物联支付签约'
    }
  },
  computed: {
    ...mapGetters('freebankcard', ['getBankRegInfo', 'getFreeMessage', 'getFreeOriginUrl']),
  },
  methods: {
    goBack() {
      if (this.fromFlag) {
        this.$router.replace(`/orderCashier/${this.fromFlag}?addcard=${this.status}`)
        return
      }
      if (this.status && this.$util.isHaierApp()) {
        instance.initDeviceReady().then(function() {
          instance.closeH5ContainerView();
        });
      } else {
        this.$router.replace('/freepwdpay?ucid=' + this.getBankRegInfo.uc_user_id)
      }
    }
  }
}
</script>

